import React from 'react';
import styles from './PropertyOverview.module.css';

import OverviewImage from '../../Assets/Overview.jpg'

const PropertyOverview = () => {
  return (
    <div className={styles.container}>
      <div className={styles.overviewSection}>
        <h2 className={styles.heading}>OVERVIEW</h2>
        
        <p className={styles.description}>
          Ever wondered what would it feel like to live by the trees? Now launching homes where 1100 trees are your neighbours. Ever imagined picking fruits from your backyard instead of a supermarket? With an orchard of over 100+ fruit-bearing trees, now you can embrace #LifeByTheTrees.
        </p>
        
        <div className={styles.imageContainer}>
          <img 
            src={OverviewImage}
            alt="Residential Property" 
            className={styles.propertyImage}
          />
          
        </div>
      </div>
    </div>
  );
};

export default PropertyOverview;