// Footer.jsx
import React from 'react';
import styles from './Footer.module.css';
import playstore from '../../Assets/playstore.png'

import appstore from '../../Assets/Appstore.png'
import logo from '../../Assets/logo2.jpg'

const Footer = () => {
  return (
    <div className={styles.container}>
      {/* Main Navigation Section */}
      <div className={styles.navGrid}>
        {/* About Column */}
        <div className={styles.column}>
          <h3 className={styles.heading}>ABOUT GODREJ PROPERTIES</h3>
          <p className={styles.text}>
            Established in 1990, Godrej Properties is the first real estate company to have ISO certification. The company is currently developing landmark projects in 12 cities across India covering over 18.58 million square meters. Godrej Properties is known to bring innovation and excellence to the real estate industry.
          </p>
        </div>

        {/* Work With Us Column */}
        <div className={styles.column}>
          <h3 className={styles.heading}>WORK WITH US</h3>
          <ul className={styles.linkList}>
            <li><a href="#" className={styles.link}>ENQUIRE</a></li>
            <li><a href="#" className={styles.link}>SCHEDULE A SITE VISIT</a></li>
          </ul>
        </div>

        {/* Our Story Column */}
        <div className={styles.column}>
          <h3 className={styles.heading}>OUR STORY</h3>
          <ul className={styles.linkList}>
            <li><a href="#" className={styles.link}>About Us</a></li>
            <li><a href="#" className={styles.link}>Management</a></li>
            <li><a href="#" className={styles.link}>Sustainability</a></li>
            <li><a href="#" className={styles.link}>Design</a></li>
          </ul>
        </div>

        {/* Projects Column */}
        <div className={styles.column}>
          <h3 className={styles.heading}>PROJECTS</h3>
          <ul className={styles.linkList}>
            <li><a href="#" className={styles.link}>Residential</a></li>
            <li><a href="#" className={styles.link}>Commercial</a></li>
          </ul>
        </div>

        {/* Media Center Column */}
        <div className={styles.column}>
          <h3 className={styles.heading}>MEDIA CENTER</h3>
          <ul className={styles.linkList}>
            <li><a href="#" className={styles.link}>In The News</a></li>
            <li><a href="#" className={styles.link}>Press Release</a></li>
            <li><a href="#" className={styles.link}>Media Gallery</a></li>
          </ul>
        </div>

        
      </div>

      {/* Social Media Section */}
      <div className={styles.socialSection}>
        <span className={styles.followText}>FOLLOW US ON</span>
        <div className={styles.socialIcons}>
          <a href="#" className={styles.socialIcon}>
            <span className={styles.facebookIcon}></span>
          </a>
          <a href="#" className={styles.socialIcon}>
            <span className={styles.twitterIcon}></span>
          </a>
          <a href="#" className={styles.socialIcon}>
            <span className={styles.instagramIcon}></span>
          </a>
          <a href="#" className={styles.socialIcon}>
            <span className={styles.youtubeIcon}></span>
          </a>
        </div>
      </div>

      {/* App Download Section */}
      <div className={styles.appSection}>
        <a href="#" className={styles.appLink}>
          <img src={playstore} alt="Download on App Store" className={styles.appImage} />
        </a>
        <a href="#" className={styles.appLink}>
          <img src={appstore} alt="Get it on Google Play" className={styles.appImage} />
        </a>
      </div>

      {/* Logo and Copyright Section */}
      <div className={styles.footerBottom}>
        <div className={styles.logoContainer}>
          <img src={logo} alt="Godrej Properties Logo" className={styles.logo} />
        </div>
        <div className={styles.copyrightSection}>
          <span className={styles.copyright}>Copyright © 2025. Godrej Properties</span>
          <a href="#" className={styles.footerLink}>Disclaimer</a>
          <a href="#" className={styles.footerLink}>Sitemap</a>
        </div>
    
      </div>
    </div>
  );
};

export default Footer;