import React from 'react';
import styles from './Location.module.css';

// This component will implement a Google Maps integration
const GoogleMap = () => {
  // In a real implementation, you would use the Google Maps API or a library like @react-google-maps/api
  // For this example, I'm creating a working embed approach that doesn't require API keys
  return (
    <div className={styles.mapWrapper}>
      <iframe 
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d15127.961603075262!2d73.70620037479043!3d18.675887094315833!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2b9dd29585bf9%3A0x2bbc2561a2cf8d19!2sMCA%20International%20Stadium!5e0!3m2!1sen!2sus!4v1711321560985!5m2!1sen!2sus" 
        width="100%" 
        height="100%" 
        style={{ border: 0 }} 
        allowFullScreen="" 
        loading="lazy" 
        referrerPolicy="no-referrer-when-downgrade"
        title="Google Maps - Gahunje, Pune"
        className={styles.mapIframe}
      ></iframe>
    </div>
  );
};

const Location = () => {
  return (
    <div className={styles.neighborhoodContainer}>
      <h2 className={styles.sectionTitle}>NEIGHBOURHOOD</h2>
      
      <p className={styles.description}>
        One of the top residential hubs in Pune and strategically located in the outskirts, Mamurdi is the first town after the Mumbai-Pune Expressway.
        Located in North-Western Pune, it is surrounded
      </p>
      
      <div className={styles.mapContainer}>
        <div className={styles.addressBox}>
          <h3 className={styles.addressTitle}>ADDRESS</h3>
          <p className={styles.addressText}>
            Sai Nagar, Gahunje Rd, near MCA's<br />
            International Stadium, St Tukaram Nagar,<br />
            Pune, Maharashtra - 412101
          </p>

          <div className={styles.amenityItem}>
            <div className={styles.amenityIcon}>
              <svg viewBox="0 0 24 24" width="40" height="40">
                <path d="M15,3H9v2h6V3z M13,13h-2v-2h2V13z M15,9h-2V7h2V9z M15,17h-2v-2h2V17z M15,21h-2v-2h2V21z M11,13H9v-2h2V13z M11,17H9v-2h2V17z M11,21H9v-2h2V21z M11,9H9V7h2V9z M7,13H5v-2h2V13z M7,17H5v-2h2V17z M7,21H5v-2h2V21z M7,9H5V7h2V9z M19,13h-2v-2h2V13z M19,17h-2v-2h2V17z M19,21h-2v-2h2V21z M19,9h-2V7h2V9z M21,5v18H3V5H21z" fill="currentColor" />
              </svg>
            </div>
            <div className={styles.amenityText}>
              <p className={styles.amenityTime}>15 mins' drive time to B.K. Birla Center</p>
              <p className={styles.amenityInfo}>For Education</p>
            </div>
          </div>

          <div className={styles.amenityItem}>
            <div className={styles.amenityIcon}>
              <svg viewBox="0 0 24 24" width="40" height="40">
                <path d="M19,7h-1V5h-2v2h-8V5H6v2H5c-1.1,0-2,0.9-2,2v10c0,1.1,0.9,2,2,2h14c1.1,0,2-0.9,2-2V9C21,7.9,20.1,7,19,7z M5,9h14v2H5V9z M5,19v-6h14v6H5z M12,10h-4v4h4V10z" fill="currentColor" />
              </svg>
            </div>
            <div className={styles.amenityText}>
              <p className={styles.amenityTime}>15 mins' drive time to Ojas Multispeciality</p>
              <p className={styles.amenityInfo}>Hospital</p>
            </div>
          </div>

          <div className={styles.amenityItem}>
            <div className={styles.amenityIcon}>
              <svg viewBox="0 0 24 24" width="40" height="40">
                <path d="M12,2C6.48,2,2,6.48,2,12s4.48,10,10,10s10-4.48,10-10S17.52,2,12,2z M12,20c-4.41,0-8-3.59-8-8s3.59-8,8-8s8,3.59,8,8S16.41,20,12,20z M12,9c-1.66,0-3,1.34-3,3s1.34,3,3,3s3-1.34,3-3S13.66,9,12,9z" fill="currentColor" />
              </svg>
            </div>
            <div className={styles.amenityText}>
              <p className={styles.amenityTime}>3 mins' drive time to MCA Stadium</p>
            </div>
          </div>

          
        </div>
        
        <div className={styles.map}>
          <GoogleMap />
          <div className={styles.mapPin}>
            <svg viewBox="0 0 24 24" width="30" height="30" fill="#e53935">
              <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z" />
            </svg>
          </div>
        </div>
      </div>
      
     
    </div>
  );
};

export default Location;













