// PlanViewer.jsx
import React, { useState } from 'react';
import styles from './Plans.module.css';

import masterplan from '../../Assets/masterplan.jpg';
import floorplan1 from '../../Assets/floorplan1.webp';
import floorplan2 from '../../Assets/floorplan2.jpg';
import floorplan3 from '../../Assets/floorplan3.jpg';
import floorplan4 from '../../Assets/floorplan4.jpg';
import floorplan5 from '../../Assets/floorplan5.jpg';
import floorplan6 from '../../Assets/floorplan6.jpg';

import unitplan1 from '../../Assets/unitplan1.jpg';
import unitplan2 from '../../Assets/unitplan2.jpg'

const PlanViewer = () => {
  // State for active tab, selected image, and modal visibility
  const [activeTab, setActiveTab] = useState('floor-plans');
  const [selectedImage, setSelectedImage] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  // Sample data for plans - replace with your actual data
  const plans = {
    'master-plan': [
      {
        id: 'master-layout',
        title: 'MASTER LAYOUT PLAN',
        location: 'MAMURDI, PUNE',
        projectName: 'GODREJ FOREST GROVE',
        image: masterplan // Replace with actual image path
      }
    ],
    'floor-plans': [
      {
        id: 'tower-a1',
        title: 'Godrej Forest Grove TowerA1',
        image: floorplan1 // Replace with actual image path
      },
      {
        id: 'tower-a2',
        title: 'Godrej Forest Grove TowerA2',
        image: floorplan2 // Replace with actual image path
      },
      {
        id: 'tower-a4',
        title: 'Godrej Forest Grove TowerA4',
        image: floorplan3 // Replace with actual image path
      }
    ],
    'unit-plans': [
      {
        id: 'unit-1',
        title: '2 BHK Unit Plan',
        image: unitplan1 // Replace with actual image path
      },
      {
        id: 'unit-2',
        title: '3 BHK Unit Plan',
        image: unitplan2 // Replace with actual image path
      }
    ]
  };

  // Handler for tab changes
  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };

  // Handler for opening the modal
  const openModal = (image) => {
    setSelectedImage(image);
    setIsModalOpen(true);
    // Add class to body to prevent scrolling when modal is open
    document.body.classList.add(styles.noScroll);
  };

  // Handler for closing the modal
  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedImage(null);
    // Remove class from body to allow scrolling again
    document.body.classList.remove(styles.noScroll);
  };

  return (
    <div className={styles.planViewer}>
      {/* Header */}
      <div className={styles.planHeader}>
        <h2 className={styles.planTitle}>PLANS</h2>
        <div className={styles.tabContainer}>
          <button 
            className={`${styles.tab} ${activeTab === 'master-plan' ? styles.activeTab : ''}`}
            onClick={() => handleTabChange('master-plan')}
          >
            Master Plan
          </button>
          <button 
            className={`${styles.tab} ${activeTab === 'floor-plans' ? styles.activeTab : ''}`}
            onClick={() => handleTabChange('floor-plans')}
          >
            Floor Plans
          </button>
          <button 
            className={`${styles.tab} ${activeTab === 'unit-plans' ? styles.activeTab : ''}`}
            onClick={() => handleTabChange('unit-plans')}
          >
            Unit Plans
          </button>
        </div>
      </div>

      {/* Plan Content */}
      <div className={styles.planContent}>
        {activeTab === 'master-plan' && (
          <div className={styles.masterPlanContainer}>
            {plans['master-plan'].map((plan) => (
              <div key={plan.id} className={styles.masterPlanItem}>
                <div className={styles.masterPlanInfo}>
                  <div className={styles.location}>{plan.location}</div>
                  <h3 className={styles.masterPlanTitle}>{plan.title}</h3>
                  <div className={styles.projectName}>{plan.projectName}</div>
                </div>
                <div 
                  className={styles.masterPlanImage}
                  onClick={() => openModal(plan.image)}
                >
                  <img src={plan.image} alt={plan.title} />
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'floor-plans' && (
          <div className={styles.floorPlansContainer}>
            {plans['floor-plans'].map((plan) => (
              <div key={plan.id} className={styles.floorPlanItem}>
                <div className={styles.floorPlanTitle}>{plan.title}</div>
                <div 
                  className={styles.floorPlanImage}
                  onClick={() => openModal(plan.image)}
                >
                  <img src={plan.image} alt={plan.title} />
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'unit-plans' && (
          <div className={styles.unitPlansContainer}>
            {plans['unit-plans'].map((plan) => (
              <div key={plan.id} className={styles.unitPlanItem}>
                <div className={styles.unitPlanTitle}>{plan.title}</div>
                <div 
                  className={styles.unitPlanImage}
                  onClick={() => openModal(plan.image)}
                >
                  <img src={plan.image} alt={plan.title} />
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Navigation arrows */}
      <div className={styles.navigation}>
        <button className={styles.navArrow}>
          <span className={styles.arrowLeft}></span>
        </button>
        <button className={styles.navArrow}>
          <span className={styles.arrowRight}></span>
        </button>
      </div>

      {/* Image Modal */}
      {isModalOpen && (
        <div className={styles.modal}>
          <div className={styles.modalOverlay} onClick={closeModal}></div>
          <div className={styles.modalContent}>
            <button className={styles.closeButton} onClick={closeModal}>
              <span className={styles.closeIcon}>×</span>
            </button>
            <div className={styles.modalImageContainer}>
              <img 
                src={selectedImage} 
                alt="Expanded Plan" 
                className={styles.modalImage} 
              />
            </div>
          </div>
        </div>
      )}

      
    </div>
  );
};

export default PlanViewer;




















