import React, { useState } from 'react';
import styles from './Gallery.module.css';
import gallery1 from '../../Assets/Gallery1.jpg';
import gallery2 from '../../Assets/Gallery2.jpg';
import gallery3 from '../../Assets/Gallery3.jpg';

const GallerySection = () => {
  const [showVideoModal, setShowVideoModal] = useState(false);

  const galleryImages = [
    { src: gallery1, alt: "Aerial view of Godrej Forest Grove green spaces and tennis court", caption: "Exterior 01" },
    { src: gallery2, alt: "Godrej Forest Grove residential tower at sunset", caption: "Exterior 02" },
    { src: gallery3, alt: "Swimming pool with lounge chairs at Godrej Forest Grove clubhouse", caption: "Clubhouse" }
  ];

  const youtubeVideos = [
    "https://www.youtube.com/embed/M68IcqIcC54",
    "https://www.youtube.com/embed/iUzM6ADImKg", // Converted from youtu.be
    "https://www.youtube.com/embed/mfB3LIbQQyA"  // Converted from youtu.be
  ];
  

  return (
    <div className={styles.container}>
      <h2 className={styles.galleryHeading}>GALLERY</h2>
      
      <p className={styles.description}>
        Nature and nurture, all at once. Grow in the thriving spaces of Godrej Forest Grove.
      </p>
      
      <div className={styles.galleryGrid}>
        {galleryImages.map((image, index) => (
          <div key={index} className={styles.galleryItem}>
            <img src={image.src} alt={image.alt} className={styles.galleryImage} />
            <p className={styles.imageCaption}>{image.caption}</p>
          </div>
        ))}
      </div>

      <div className={styles.pagination}>
        <span className={styles.activeDot}></span>
        <span className={styles.dot}></span>
        <span className={styles.dot}></span>
      </div>
      
      <div className={styles.viewGalleryContainer}>
        <button className={styles.viewGalleryButton} onClick={() => setShowVideoModal(true)}>
          VIEW GALLERY
        </button>
      </div>

      {/* Video Modal */}
      {showVideoModal && (
        <div className={styles.modalOverlay} onClick={() => setShowVideoModal(false)}>
          <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
            <button className={styles.closeButton} onClick={() => setShowVideoModal(false)}>✖</button>
            <h2 className={styles.modalTitle}>Project Videos</h2>
            <div className={styles.videoGrid}>
              {youtubeVideos.map((video, index) => (
                <iframe 
                  key={index} 
                  width="100%" 
                  height="250" 
                  src={video} 
                  title={`YouTube Video ${index + 1}`} 
                  frameBorder="0" 
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                  allowFullScreen>
                </iframe>
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GallerySection;
