import React, { useState } from 'react';
import styles from './ContactForm.module.css';

const ContactForm = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phoneNumber: '',
    message: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form submitted:', formData);
  };

  return (
    <div className={styles.pageContainer}>
      <div className={styles.contentWrapper}>
        <div className={styles.leftSection}>
          <h1 className={styles.mainTitle}>Sell your home with our tailored solution for you.</h1>
          <p className={styles.description}>
            Connect with our local experts who can create a comprehensive 
            and customized plan for your home that takes into consideration 
            all avenues of marketing.
          </p>
        </div>
        <div className={styles.rightSection}>
          <h2 className={styles.contactTitle}>Let's get in touch.</h2>
          <form onSubmit={handleSubmit} className={styles.contactForm}>
            <div className={styles.nameRow}>
              <input
                type="text"
                name="firstName"
                value={formData.firstName}
                onChange={handleChange}
                placeholder="First Name"
                className={styles.halfInput}
                required
              />
              <input
                type="text"
                name="lastName"
                value={formData.lastName}
                onChange={handleChange}
                placeholder="Last Name"
                className={styles.halfInput}
                required
              />
            </div>
            <div className={styles.formGroup}>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                placeholder="Email Address"
                className={styles.fullInput}
                required
              />
            </div>
            <div className={styles.formGroup}>
              <input
                type="tel"
                name="phoneNumber"
                value={formData.phoneNumber}
                onChange={handleChange}
                placeholder="Phone Number (Optional)"
                className={styles.fullInput}
              />
            </div>
            <div className={styles.formGroup}>
              <textarea
                name="message"
                value={formData.message}
                onChange={handleChange}
                placeholder="I'd like to discuss selling with you."
                className={styles.textarea}
                required
              />
            </div>
            <div className={styles.privacyText}>
              This site is protected by reCAPTCHA and the Google 
              <a href="#" className={styles.privacyLink}> Privacy Policy</a> and 
              <a href="#" className={styles.privacyLink}> Terms of Service</a> apply.
            </div>
            <button type="submit" className={styles.submitButton}>
              SEND MESSAGE →
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ContactForm;