import React, { useState, useEffect } from 'react';
import styles from './Navbar.module.css';
import logo from '../../Assets/logo.jpg';
import { Link } from "react-scroll";
import PopupForm from '../Popupform/PopupForm';

const Navbar = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isPopupOpen, setIsPopupOpen] = useState(true);  // Auto-show form on load
  const [showContactBox, setShowContactBox] = useState(false); // Show/hide contact box

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 100);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className={`${styles.navbarWrapper} ${isScrolled ? styles.scrolled : ''}`}>
      {/* Top Navbar */}
      <div className={styles.topNavbar}>
        <div className={styles.logo}>
          <img src={logo} alt="Godrej Properties Logo" />
        </div>
        <div className={styles.navIcons}>
          {/* Call Icon - Opens Contact Box */}
          <button className={styles.iconButton} onClick={() => setShowContactBox(!showContactBox)}>
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <path d="M22 16.92v3a2 2 0 0 1-2.18 2 19.79 19.79 0 0 1-8.63-3.07 19.5 19.5 0 0 1-6-6 19.79 19.79 0 0 1-3.07-8.67A2 2 0 0 1 4.11 2h3a2 2 0 0 1 2 1.72 12.84 12.84 0 0 0 .7 2.81 2 2 0 0 1-.45 2.11L8.09 9.91a16 16 0 0 0 6 6l1.27-1.27a2 2 0 0 1 2.11-.45 12.84 12.84 0 0 0 2.81.7A2 2 0 0 1 22 16.92z"></path>
            </svg>
          </button>
        </div>
      </div>

      {/* Contact Box - Shows on Click */}
      {showContactBox && (
        <div className={styles.contactBox}>
          <h3>Contact Us</h3>
          <p>📞 +91 77560 83523</p>
        </div>
      )}

      {/* Auto-Opening Pop-up Form */}
      <PopupForm isOpen={isPopupOpen} onClose={() => setIsPopupOpen(false)} />

      {/* Bottom Navbar */}
      <div className={styles.bottomNavbar}>
        <div className={styles.navLinks}>
          <Link to="overview" smooth={true} duration={500} offset={-100} className={styles.navLink}>OVERVIEW</Link>
          <Link to="location" smooth={true} duration={500} offset={-100} className={styles.navLink}>LOCATION</Link>
          <Link to="plans" smooth={true} duration={500} offset={-100} className={styles.navLink}>PLANS</Link>
          <Link to="amenities" smooth={true} duration={500} offset={-100} className={styles.navLink}>AMENITIES</Link>
          <Link to="gallery" smooth={true} duration={500} offset={-100} className={styles.navLink}>GALLERY</Link>
          <Link to="contact" smooth={true} duration={500} offset={-100} className={styles.navLink}>CONTACT</Link>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
